.write{
padding-top: 50px;

}
.writeImg{

margin-left:5%;
width: 90%;
height: 250px;
border-radius: 10px;
object-fit: cover;
}
.writeForm{
   position: relative;
}
.writeFormGroup{
    margin-left: 150px;
   display: flex;
   align-items: center;
}
.writeIcon{
width: 25px;
height: 25px;
font-size: 20px;
color: #666;
cursor: pointer;
border: 2px solid;
border-radius: 50%;
display: flex;
align-items: center;
justify-content: center;
}

.writeInput{
   font-size: 30px; 
   border: none;
   padding: 20px;
   width: 70vh;
}
.writeInput:focus{
 outline: none;
}

.writeText{

 font-size: 20px;   
 height: 100vh;
}

.writeSubmit{
position: absolute;
top: 20px;
right: 50px;
color: white;
background-color: teal;
padding: 10px;
border: none;
border-radius: 5px;
cursor: pointer;
font-size: 16px;
}