.singlePost{
    flex: 9;
  
}
.singlePostWrapper{
padding: 20px;
padding-right: 0;

}

.singlePosImg{
    width: 100%;
    height: 300px;
  border-radius: 5px;
  object-fit: cover;
   
}

.singlePostTitle{
text-align: center;
font-family: "Lora", serif;

font-size: 20px;

}
.singlePostEdit{
float: right;
font-size: 16px;

}

.singlePostIcon{
    margin-left: 10px;
    cursor: pointer;
}

.singlePostIcon:first-child{
    color: teal;
}
.singlePostIcon:last-child{
    color: coral;
}
.singlePostInfo{
margin-bottom: 20px;
display: flex;
justify-content: space-between;
font-family: 'Varela Round', sans-serif;
color: #b39656;
}

.singlePostDesc{

    color: #666 ;
    font-size: 18px;
    line-height: 25px;
}
.singlePostDesc::first-letter{
margin-left: 20px;
font-size: 30px;
font-weight: 600;
}